#dao {
  position: relative;
  width: 100%;
  max-width: 750px;
  margin: 0 auto;
  border: none;
  font-size: 14px;
  align-items: center;
  text-align: center;
  letter-spacing: -0.3px;
  text-align: left;

  .header {
    background: #3782fa;
    border-radius: 2px;
    // height: 46px;
    padding-left: 18px;
    color: #ffffff;
    font-size: 14px;
    width: 100%;
    line-height: 46px;
    margin-bottom: 18px;
  }

  .tokens {
    width: 100%;
    background: #fff;
    color: #333;
    padding: 20px 20px 32px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);

    legend {
      margin-top: 20px;
      color: #333;
      display: block;
    }

    p {
      font-size: 13px;
      line-height: 24px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: -0.3px;
      color: #333;
      margin-bottom: 3px;
    }

    span {
      margin: 0 5px;
    }
  }

  .pool {
    width: 100%;
    background: #fff;
    padding-top: 10px;
    color: #333;
    margin-top: 18px;
    padding: 20px 20px 32px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);

    legend {
      margin-top: 20px;
      color: #333;
      display: block;
    }

    li {
      margin: 5px 20px;
      list-style: disc !important;
    }
  }

  .table {
    width: 100%;
    background: #fff;
    padding: 20px 20px 32px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    // padding: 0 5px 5px;
    border-collapse: collapse;
    width: 100%;
    color: #333;
    margin-bottom: 18px;
    legend {
      line-height: 44px;
    }
  }

  table {
    width: 100%;

    tr {
      border-top: 1px solid #dacdaf;
      border-bottom: 1px solid #dacdaf;
    }

    td,
    th {
      border-right: 1px solid #dacdaf;
      border-left: 1px solid #dacdaf;
      padding-left: 3px;
    }

    span {
      margin-left: 3px;
    }
  }

  .wrap {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }

  .column {
    width: 100%;
    margin: 18px auto;
    background: #fff;
    padding: 20px 20px 32px;
    border-radius: 15px;
    box-shadow: 0px 20px 40px rgb(148 148 148 / 6%);
    color: #333;
    min-height: 385px;

    fieldset {
      padding: 10px;
      // width: 50%;
      margin: 0 auto;
      max-width: 446px;
    }

    legend {
      margin: 20px auto;
      color: #333;
      display: block;
    }

    p {
      font-size: 13px;
      line-height: 30px;
      display: flex;
      align-items: center;
      text-align: center;
      letter-spacing: -0.3px;
      color: #333;
      border-bottom: 1px solid #f1f1f1;
      padding-left: 3px;
      height: 30px;
    }

    input {
      width: 100%;
      height: 28px;
      line-height: 20px;
      padding: 3px;
      border-radius: 0;
      background: #fff;
      color: #333;
      margin-bottom: 6px;
      font-size: 16px;
      line-height: 22px;
      padding: 4px 11px;
    }

    // span {
    //   margin: 0 5px;
    // }
  }

  .st-mix {
    display: flex;
    justify-content: space-between;
    margin: 10px 0 0;
    padding-bottom: 10px;
    border-bottom: 1px solid #e5e5e5;
    font-size: 13px;
    color: #999;
    height: 42px;
    line-height: 32px;

    .btn {
      width: 110px;
      height: 32px;
      line-height: 26px;
      border: 3px solid #4e545a;
      border-radius: 4px;
      text-align: center;
      color: #4e545a;
      font-size: 14px;
      cursor: pointer;
    }
  }

  hr {
    background: #dacdaf;
    border: 0;
    height: 1px;
  }

  .claimBtn {
    display: block;
    width: 100%;
    margin: 10px auto;
  }
}

@media screen and (max-width: 800px) {
  #dao {
    width: calc(100% - 30px);
    .column {
      width: 100%;
    }
  }
}
